import React, { Component } from 'react';
import { connect } from 'react-redux';
import { set_page } from '../../actions';

import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';

import LevelUP_QuestHistory_Complete from './../LevelUP_QuestHistory_Complete'
import LevelUP_QuestHistory_PvP from './../LevelUP_QuestHistory_PvP'


import restful from './../../Controller/RestfulHandler'
import dotenv from 'dotenv'
import TEXTDATA from './../../Controller/Textdata'

function TabPanel(props) {
    const { children, value, index, ...other } = props;

    return (
        <Typography
            component="div"
            role="tabpanel"
            hidden={value !== index}
            id={`simple-tabpanel-${index}`}
            aria-labelledby={`simple-tab-${index}`}
            {...other}
        >
            {value === index && <Box p={3}>{children}</Box>}
        </Typography>
    );
}

TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.any.isRequired,
    value: PropTypes.any.isRequired,
};

function a11yProps(index) {
    return {
        id: `simple-tab-${index}`,
        'aria-controls': `simple-tabpanel-${index}`,
    };
}

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
        width: '100%',
        height: '100%',
        backgroundColor: theme.palette.background.paper,
    },
}));

export const LevelUP_QuestHistory_Scene = ({ id, name, player_properties, set_page }) => {
    const classes = useStyles();
    const [value, setValue] = React.useState(0);

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };

    return (
        <div className={classes.root}>
            <AppBar position="static">
                <Tabs value={value} onChange={handleChange} aria-label="quest history">
                    <Tab label={TEXTDATA.MY_QUEST_HISTORY_COMPLETE_TAB_TITLE} {...a11yProps(0)} />
                    <Tab label={TEXTDATA.MY_QUEST_HISTORY_DROP_TAB_TITLE} {...a11yProps(1)} />
                    <Tab label={TEXTDATA.MY_QUEST_HISTORY_PVP_TAB_TITLE} {...a11yProps(2)} />
                </Tabs>
            </AppBar>
            <TabPanel value={value} index={0}>
                <LevelUP_QuestHistory_Complete/>
        </TabPanel>
            <TabPanel value={value} index={1}>
                Item Two
        </TabPanel>
            <TabPanel value={value} index={2}>
                <LevelUP_QuestHistory_PvP/>
        </TabPanel>
        </div>
    );
}

const mapStateToProps = (state) => ({
    id: state.account.id,
    name: state.account.name,
    player_properties: state.properties
});

const mapDispatchToProps = { set_page };

export default connect(mapStateToProps, mapDispatchToProps)(LevelUP_QuestHistory_Scene);

