import React from "react";
import { useDispatch, connect } from 'react-redux';
import { logout } from '../actions';

import { lighten, makeStyles, withStyles } from "@material-ui/core/styles";
import { Grid } from "@material-ui/core";
import LinearProgress from "@material-ui/core/LinearProgress";
import player from "./../Controller/Player"



function createProgressBar(bar_height, color, parent_progress) {
    const newComponent = withStyles({
      root: {
        backgroundColor: lighten(color, 0.5)
      },
      bar: {
        backgroundColor: color
      }
    })(parent_progress);
    return newComponent;
}

function newStyle(bar_height) {
  const useStyle = makeStyles(theme => ({
    root: {
      flexGrow: 1
    },
    margin: {
      margin: theme.spacing(1)
    },
    progressLabel: {
      position: "absolute",
      width: "100%",
      height: "100%",
      zIndex: 1,
      maxHeight: bar_height, // borderlinearprogress root.height
      textAlign: "center",
      display: "flex",
      alignItems: "center",
      "& span": {
        width: "100%"
      }
    },
    LevelLabel: {
      position: "absolute",
      width: "100%",
      height: "100%",
      zIndex: 1,
      maxHeight: bar_height, // borderlinearprogress root.height
      textAlign: "left",
      display: "flex",
      alignItems: "center",
      "& span": {
        width: "100%"
      }
    }
  }));
  return useStyle;
}

export const LevelUP_Stat_ProgressBar = ({player_properties, logout, value, color}) => {
  const bar_height = 20;
  const BorderLinearProgress = withStyles({
    root: {
      height: bar_height
    },
    bar: {
      borderRadius: 30
    }
  })(LinearProgress);
  
  const NewProgreeBar = createProgressBar(bar_height, color, BorderLinearProgress);

  const useStyle = newStyle(bar_height);
  const classes = useStyle();

  const progressNumber = value*1;
  const [completed, setCompleted] = React.useState(progressNumber);

  //console.log('id : ' + player_properties.stat_strength);
  const strength_level = player.get_strength_level_from_exp(player_properties.stat_strength);

  return (
    <div className={classes.root}>
      <Grid container spacing={1} justify="space-between">
        <Grid item xs={12}>
          <div className={classes.LevelLabel}>
            <span>Lv.{value}</span>
          </div>

          <div className={classes.progressLabel}>
            <span>{value}</span>
          </div>
          <NewProgreeBar variant="determinate" value={progressNumber}/>
        </Grid>
      </Grid>
    </div>
  );
}


const mapStateToProps = (state) => ({
  player_properties: state.properties
});

const mapDispatchToProps = { logout };

export default connect(mapStateToProps, mapDispatchToProps)(LevelUP_Stat_ProgressBar);