import React, { Component } from 'react';
import { connect } from 'react-redux';
import { set_page, select_quest } from '../actions';

import { makeStyles } from '@material-ui/core/styles';
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import GridListTileBar from '@material-ui/core/GridListTileBar';
import ListSubheader from '@material-ui/core/ListSubheader';
import IconButton from '@material-ui/core/IconButton';
import InfoIcon from '@material-ui/icons/Info';

import LevelUP_MyQuest_ProgressBar from './LevelUP_MyQuest_ProgressBar'
import dotenv from 'dotenv'
//const urlMetadata = require('url-metadata')

const useStyles = makeStyles(theme => ({
  root: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'space-around',
    overflow: 'hidden',
    backgroundColor: '#ffffff',
  },
  gridList: {
    flexWrap: 'nowrap', //가로 배차
    flexDirection: 'column',
    position: 'absolute',
    flexGrow: 1,
    left: 5,
    right: 0,
    top: 56,
    bottom: 160,
  },
  gridList_desc: {
    position: 'absolute',
    left: 105,
    right: 0,
    top : 5,
    bottom : 5,
  },
  gridList_Tile: {
    width: '100%',
    height: '100%',
    color: '#000000',
  },
  gridList_TileBar: {
    height: '100%',
    backgroundColor: '#dddddd',
    color: '#000000',
  },
  gridList_TileImg: {
    width: 100,
    height: '100%',
    position: 'absolute',
    left: 0,
  },
  gridList_Tile_TitleSpan: {
    color: '#000000',
    fontSize: 25,
    fontWeight: 600,
    position: 'absolute',
    left: 0,
  },
  gridList_Tile_SubTitleSpan: {
    color: '#000000',
    position: 'absolute',
    left: 0,
    top: 35,
    fontSize: 15,
    fontWeight: 300,
  },
  gridList_Tile_ProgressBar: {
    position: 'absolute',
    left: 30,
    width: 200,
    top: 60,
  },
  icon: {
    color: 'rgba(255, 255, 255, 0.54)',
  },
}));
export const LevelUP_MyQuest_Card = ({id, name, tileData, set_page, select_quest}) => {
  const classes = useStyles();

  const handleClick = (id, questNode) => {
    console.log('GridListTile select quest:' + id);
    select_quest(id, questNode);
    set_page('progress_quest');
  }

  const getTileNode = (key, questNode) => {  
    console.log(questNode);
    
    var progressBar = undefined;
    if (undefined != questNode.list && 0 < questNode.list.length) {
      var stepPercent = parseInt(questNode.step*100/questNode.list.length);
      console.log('stepPercent:' + stepPercent);
      progressBar = <div className={classes.gridList_Tile_ProgressBar}><LevelUP_MyQuest_ProgressBar title={questNode.description} value={stepPercent} color='rgb(60,60,60)' /></div>;
    }
    return (
      <GridListTile key={key} className={classes.gridList_Tile} onClick={() => { handleClick(key, questNode); }} >
        <GridListTileBar className={classes.gridList_TileBar} />
        <div className={classes.gridList_desc}>
          <span className={classes.gridList_Tile_TitleSpan}>{questNode.title}</span>
          <span className={classes.gridList_Tile_SubTitleSpan}> {questNode.description}</span>
          {progressBar}
        </div>
        <img src={process.env.REACT_APP_RESTFUL_SERVER_IP + questNode.img} alt={questNode.title} className={classes.gridList_TileImg} />
      </GridListTile>
    );
  }

//tile 파싱해서 타이틀, 제목, 이미지 얻어오기 
  return (
    <div className={classes.root}>
      <GridList cellHeight={180} cols={1} className={classes.gridList} >
        {Object.entries(tileData).map(([key, tile]) => {
          const tileNode = getTileNode(key, tile)
          return tileNode;
        })}
      </GridList>
    </div>
  );
}

const mapStateToProps = (state) => ({
    id: state.account.id,
    name: state.account.name
});

const mapDispatchToProps = { set_page, select_quest };

export default connect(mapStateToProps, mapDispatchToProps)(LevelUP_MyQuest_Card);