import React, { Component } from 'react';
import { useDispatch, connect } from 'react-redux';
import { set_page } from '../actions';

import { lighten, makeStyles, withStyles } from '@material-ui/core/styles';
import BottomNavigation from '@material-ui/core/BottomNavigation';
import BottomNavigationAction from '@material-ui/core/BottomNavigationAction';
import FolderIcon from '@material-ui/icons/Folder';
import RestoreIcon from '@material-ui/icons/Restore';
import FavoriteIcon from '@material-ui/icons/Favorite';
import LocationOnIcon from '@material-ui/icons/LocationOn';

import my_info_icon_img from './../images/my_info_icon.jpg'
import my_quest_icon_img from './../images/my_quest_icon.jpg'
import quest_board_icon_img from './../images/quest_board_icon.jpg'
import home_icon_img from './../images/home_icon.jpg'

import AppBar from '@material-ui/core/AppBar';
import Grid from "@material-ui/core/Grid";
import LinearProgress from '@material-ui/core/LinearProgress';
import LevelUP_Stat_ProgressBar from './LevelUP_Stat_ProgressBar'

const useStyles = makeStyles(theme => ({
    root: {
        width: '100%',
        // height: '100%',
        bottom: 0,
        position: 'fixed',
        flexGrow: 1,
    },
    navigationBar: {
        height: 70,
    }
  }));

export const LevelUP_NavigationBar = ({id, name, set_page}) => {

    const classes = useStyles();
    const [value, setValue] = React.useState('home');
  
    const handleChange = (event, newValue) => {
        set_page(newValue);
        setValue(newValue);
    };

    // 
    return (
        <div className={classes.root}>  
            <LevelUP_Stat_ProgressBar color='rgb(255,50,100)' value='38'/>
            <LevelUP_Stat_ProgressBar color='rgb(255,100,255)' value='92'/>
            <LevelUP_Stat_ProgressBar color='rgb(255,100,0)' value='75'/>
            <LevelUP_Stat_ProgressBar color='rgb(0,0,255)' value='35'/>
            <BottomNavigation value={value} onChange={handleChange} className={classes.navigationBar}>
                <BottomNavigationAction label="home" value="home" icon={<img src={home_icon_img}/>}  />
                <BottomNavigationAction label="my info" value="my_info" icon={<img src={my_info_icon_img}/>}  />
                <BottomNavigationAction label="my quest" value="my_quest" icon={<img src={my_quest_icon_img}/>}  />
                <BottomNavigationAction label="quest board" value="quest_board" icon={<img src={quest_board_icon_img}/>}  />
                <BottomNavigationAction label="my history" value="quest_history" icon={<img src={quest_board_icon_img}/>}  />
            </BottomNavigation>
      </div>
    );

}

const mapStateToProps = (state) => ({
    id: state.account.id,
    name: state.account.name
});

const mapDispatchToProps = { set_page };

export default connect(mapStateToProps, mapDispatchToProps)(LevelUP_NavigationBar);