import React, { Component } from 'react';
import { useDispatch, connect } from 'react-redux';
import { logout } from '../actions';

import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import AssessmentIcon from '@material-ui/icons/Assessment';
import SearchIcon from '@material-ui/icons/Search';
import TEXTDATA from './../Controller/Textdata'

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
    // top: 0,
    //position: 'static',
    width: '100%',
    // top: 'auto',
    // bottom: 0,
    //height: 40,
    zIndex: 1,
  },
  title: {
    flexGrow: 1,
  },
}));

const getTitle = (page) => {
  switch (page) {
    case 'home':
      return TEXTDATA.HOME_PAGE_TITLE;
    case 'my_info':
      return TEXTDATA.MY_INFO_PAGE_TITLE;
    case 'my_quest':
      return TEXTDATA.MY_QUEST_PAGE_TITLE;
    case 'quest_board':
      return TEXTDATA.MY_QUEST_BOARD_PAGE_TITLE;
    case 'quest_history':
      return TEXTDATA.MY_QUEST_HISTORY_PAGE_TITLE;
    default:
      return '';
  }
}
export const LevelUP_AppBar = ({page}) => {
    const classes = useStyles();
    const title_text = getTitle(page);
    //console.log(title_text + ' : ' + page);

    const onSearch = () => {
        console.log('onSearch');
    };

    const onRank = () => {
        console.log('onRank');
    };

    return (
      <div className={classes.root}>
        <AppBar position="static">
          <Toolbar>
            <Typography variant="h6" align="left" className={classes.title}>
              {title_text}
            </Typography>

            <IconButton aria-label="search" color="inherit" onClick={onSearch}>
                <SearchIcon />
            </IconButton>

            <IconButton color="inherit" aria-label="rank" onClick={onRank}>
                <AssessmentIcon />
            </IconButton>

          </Toolbar>
        </AppBar>
      </div>
    );
}

const mapStateToProps = (state) => ({
    id: state.account.id,
    name: state.account.name,
    page: state.scene.current_page
});

export default connect(mapStateToProps)(LevelUP_AppBar);
